<template>
  <v-card>
    <v-card-title>
      Serviços

      <v-btn color="red accent-1" right absolute fab @click="add()">
        <v-icon>mdi-plus</v-icon>
      </v-btn>
    </v-card-title>
    <v-card-text>
      <v-col>
        <v-row>
          <v-col cols="6" md="3">
            <v-text-field
              v-model="filter.protocol"
              label="Protocolo"
            ></v-text-field>
          </v-col>
          <v-col cols="6" md="4">
            <v-text-field v-model="filter.title" label="Nome"></v-text-field>
          </v-col>
          <v-col cols="6" md="5">
            <v-select
              v-model="filter.status"
              multiple
              :clearable="true"
              :items="filter.statusList"
              label="Situação"
            ></v-select>
          </v-col>
          <v-col cols="6" md="3">
            <v-select
              v-model="filter.type"
              :clearable="true"
              :items="filter.types"
              label="Tipo"
            ></v-select>
          </v-col>
          <v-col cols="6" md="2">
            <v-select
              v-model="filter.billingStatus"
              :clearable="true"
              :items="filter.billingStatusList"
              label="Sit Faturamento"
            ></v-select>
          </v-col>
          <v-col cols="5" md="3">
            <v-menu
              ref="dialogConclusionIni"
              v-model="modalConclusionIni"
              :close-on-content-click="false"
              :return-value.sync="filter.dtConclusionIni"
              transition="scale-transition"
              offset-y
              min-width="auto"
            >
            
              <template v-slot:activator="{ on, attrs }">
                <v-text-field
                  v-model="computedConclusionIniFormatted"
                  label="Data Inicial de Conclusão"
                  prepend-icon="mdi-calendar"
                  readonly
                  v-bind="attrs"
                  v-on="on"
                ></v-text-field>
              </template>
              <v-date-picker
                v-model="filter.dtConclusionIni"
                locale="pt-br"
                scrollable
              >
                <v-spacer></v-spacer>

                <v-btn text color="warning" @click="filter.dtConclusionIni =  null">
                  limpa
                </v-btn>

                <v-btn text color="primary" @click="modalConclusionIni = false">
                  Cancela
                </v-btn>
                <v-btn
                  text
                  color="primary"
                  @click="$refs.dialogConclusionIni.save(filter.dtConclusionIni)"
                >
                  OK
                </v-btn>
              </v-date-picker>
            </v-menu>
          </v-col>
          <v-col cols="5"  md="3">
             <v-menu
              ref="dialogConclusionEnd"
              v-model="modalConclusionEnd"
              :close-on-content-click="false"
              :return-value.sync="filter.dtConclusionEnd"
              transition="scale-transition"
              offset-y
              min-width="auto"
            >
           
              <template v-slot:activator="{ on, attrs }">
                <v-text-field
                  v-model="computedConclusionEndFormatted"
                  label="Data Final de Conclusão"
                  prepend-icon="mdi-calendar"
                  readonly
                  v-bind="attrs"
                  v-on="on"
                ></v-text-field>
              </template>
              <v-date-picker
                v-model="filter.dtConclusionEnd"
                locale="pt-br"
                scrollable
              >
                <v-spacer></v-spacer>

                <v-btn text color="warning" @click="filter.dtConclusionEnd =  null">
                  limpa
                </v-btn>

                <v-btn text color="primary" @click="modalConclusionEnd = false">
                  Cancela
                </v-btn>
                <v-btn
                  text
                  color="primary"
                  @click="$refs.dialogConclusionEnd.save(filter.dtConclusionEnd)"
                >
                  OK
                </v-btn>
              </v-date-picker>
            </v-menu>
          </v-col>

          <v-col cols="1" md="1">
            <v-btn @click="onClickFilter()" text
              ><v-icon>mdi-magnify</v-icon></v-btn
            >
          </v-col>
        </v-row>
      </v-col>

      <v-data-table
        v-bind:headers="headers"
        :items="orders"
        class="elevation-1"

        @click:row="handleClick"
      >
        <template v-slot:[`item.customerName`]="{ item }">
        {{item.customerName | truncate(20 , '...') }}
        </template>
        <template v-slot:[`item.title`]="{ item }">
         {{item.title | truncate(20 , '...') }}
        </template>
        <template v-slot:[`item.creation`]="{ item }">
          {{ formatDate(item.creation) }}
        </template>
         <template v-slot:[`item.conclusion`]="{ item }">
          {{ formatDate(item.conclusion) }}
        </template>
        >
      </v-data-table>
    </v-card-text>
    <order v-bind:order="item" v-bind:activate.sync="showOrder"></order>
  </v-card>
</template>

<script>
import firebase from "firebase";
// import{query, collection, where, getDocs} from "firebase/firestore";
import moment from "moment";
import Order from "./Order.vue";
import { VMoney } from "v-money";

export default {
  directives: { money: VMoney },
  name: "Customer",
  components: {
    Order,
  },
  data() {
    return {
      showOrder: false,
      headers: [
        { text: "Cliente", value: "customerName", align: "left" },
        { text: "Nome do Processo", value: "title", align: "left" },
        { text: "Tipo", value: "type", align: "left" },
        { text: "Protocolo", value: "protocol", align: "left" },
        { text: "Situação", value: "status", align: "left" },
        { text: "Valor", value: "price", align: "left" },
        { text: "Faturamento", value: "billingStatus", align: "left" },
        { text: "Criação", value: "creation", align: "left" },
        { text: "Conclusão", value: "conclusion", align: "left" },
      ],
      filter: {
        title: "",
        protocol: "",
        status: [],
        type: "",
        billingStatus: "",
        statusList: ["Aberto", "A. Doc", "Andamento", "Final"],
        billingStatusList: ["Aberto", "Faturado", "Pago"],
        types: ["Constituição", "Transformação", "Extinção", "Cisão"],
        dtConclusionIni:null,
        dtConclusionEnd:null,
      },
      modalConclusionIni: false,
      modalConclusionEnd: false,
      orders: [],
      item: {},
      teste: 123.45,
      vallll: "22",
      money: {
        decimal: ",",
        thousands: ".",
        prefix: "R$ ",
        suffix: " #",
        precision: 2,
        masked: false /* doesn't work with directive */,
      },
    };
  },
  computed:{
    computedConclusionIniFormatted() {
      return this.formatDate(this.filter.dtConclusionIni);
    },
      computedConclusionEndFormatted() {
      return this.formatDate(this.filter.dtConclusionEnd);
    },
  },

  created() {
    this.orders = new Array();  
    this.loadOrders();
  },

  methods: {
    onClickFilter() {
      this.loadOrders();
    },
    recarrega() {
      this.teste = 200.2;
    },
    handleClick(value) {
      this.item = value;
      this.showOrder = true;
    },
    formatDate(value) {
      if (!value) return "--";

      return moment(value).format("DD/MM/YY");
    },
    addFilters(query) {
      if (this.filter.status.length > 0) {
        query = query.where("status", "in", this.filter.status);
      }
      if (this.filter.type && this.filter.type.length > 0) {
        query = query.where("type", "==", this.filter.type);
      }
      if (this.filter.billingStatus && this.filter.billingStatus.length > 0) {
        query = query.where("billingStatus", "==", this.filter.billingStatus);
      }
      if (this.filter.protocol.length > 0) {
        query = query
          .orderBy("protocol")
          .where("protocol", ">=", this.filter.protocol)
          .where("protocol", "<=", this.filter.protocol + "~");
      }
      if (this.filter.title.length > 0) {
        query = query
          .orderBy("title")
          .where("title", ">=", this.filter.title)
          .where("title", "<=", this.filter.title + "~");
      }
      if(this.filter.dtConclusionIni && this.filter.dtConclusionEnd){
        query = query
        .where("conclusion",">=", this.filter.dtConclusionIni)
        .where("conclusion","<=", this.filter.dtConclusionEnd)
        
      }
      return query;
    },
    // newFilter(){
    //   const q = query(
    //     collection(firebase.firestore,"order"),
    //     where("status", "in", this.filter.status),
    //     where("type", "in", this.filter.type)
    //   )
    // },
    loadOrders() {
      var query = firebase.firestore().collection("order");
      query = this.addFilters(query);
      // query = query.orderBy("creation", "desc");

      query.onSnapshot((snap) => {
        this.orders = [];
        snap.forEach(async (doc) => {
          let ord = doc.data();
          ord.id = doc.id;
          if (ord.customer)
            await ord.customer.get().then((snap) => {
              ord.customerName = snap.data().name;
            });
          this.orders.push(ord);
        });
      });
    },

    add() {
      this.item = new Object();
      this.showOrder = true;
    },
  },
};
</script>

<style lang="scss" scoped>
$data-table-regular-row-font-size:8em !default;
</style>>
